import Vue from 'vue'
import VueRouter from 'vue-router'
import Maket1 from '../views/Maket1.vue'
import Maket2 from '../views/Maket2.vue'
import Maket3 from '../views/Maket3.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/maket1',
    name: 'Maket1',
    component: Maket1
  },
  {
    path: '/maket2',
    name: 'Maket2',
    component: Maket2
  },
  {
    path: '/maket3',
    name: 'Maket3',
    component: Maket3
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
